<template>
    <div class="pickup">
        <div class="auto">
            <breadcrumb />
            <h2 class="title">接送服務</h2>
            <div class="section">
                <h3 class="subTitle">服務項目</h3>
                <p>接送、包車費用 NT.$588起</p>
            </div>
            <h2 class="title">計算車費</h2>
            <el-form ref="form" label-width="80px" :label-position="labelPosition">
                <el-form-item label="接送日期">
                    <el-date-picker v-model="date" type="date" placeholder="選擇日期"></el-date-picker>
                </el-form-item>
                <el-form-item label="接送時間">
                    <el-time-picker v-model="time" placeholder="選擇時間"></el-time-picker>
                </el-form-item>
                <el-form-item label="預約車型">
                    <el-select v-model="seat" placeholder="選擇車型" class="input">
                        <el-option label="5人座" value="5"></el-option>
                        <el-option label="7人座" value="7"></el-option>
                        <el-option label="9人座" value="9"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="車款">
                    <el-select v-model="style" placeholder="選擇車型" class="input">
                        <el-option label="一般" value="general"></el-option>
                        <el-option label="進口" value="import"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="是否需要代課舉牌">
                    <el-checkbox-group v-model="sign">
                        <el-checkbox label="是" name="type"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                <el-form-item label="安全座椅">
                    <el-select v-model="safe" placeholder="選擇數量" class="input">
                        <el-option label="0" value="0"></el-option>
                        <el-option label="1" value="1"></el-option>
                        <el-option label="2" value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="出發地點">
                    <el-input v-model="fromPlace" placeholder="輸入位置" class="input"></el-input>
                </el-form-item>

                <el-form-item label="目的地點">
                    <el-input v-model="toPlace" placeholder="輸入位置" class="input"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="success" @click="submit" class="btn">試算車費</el-button>
                    <router-link to="/order" class="btn">
                        <el-button type="info">重選服務</el-button>
                    </router-link>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
import breadcrumb from '@/components/breadcrumb/index.vue';
import { mapGetters } from 'vuex';
export default {
    name: 'pickup',
    components: {
        breadcrumb
    },
    computed: {
        ...mapGetters(['labelPosition']),
        date: {
            get() {
                return this.$store.state.pickup.date;
            },
            set(val) {
                this.$store.commit('PICKUPDATE', val);
            }
        },
        time: {
            get() {
                return this.$store.state.pickup.time;
            },
            set(val) {
                this.$store.commit('PICKUPTIME', val);
            }
        },
        seat: {
            get() {
                return this.$store.state.pickup.seat;
            },
            set(val) {
                this.$store.commit('PICKUPSEAT', val);
            }
        },
        style: {
            get() {
                return this.$store.state.pickup.style;
            },
            set(val) {
                this.$store.commit('PICKUPSTYLE', val);
            }
        },
        sign: {
            get() {
                return this.$store.state.pickup.sign;
            },
            set(val) {
                this.$store.commit('PICKUPSIGN', val);
            }
        },
        safe: {
            get() {
                return this.$store.state.pickup.safe;
            },
            set(val) {
                this.$store.commit('PICKUPSAFE', val);
            }
        },
        fromPlace: {
            get() {
                return this.$store.state.pickup.fromPlace;
            },
            set(val) {
                this.$store.commit('PICKUPFROMPLACE', val);
            }
        },
        toPlace: {
            get() {
                return this.$store.state.pickup.toPlace;
            },
            set(val) {
                this.$store.commit('PICKUPTOPLACE', val);
            }
        }
    },
    methods: {
        submit() {}
    }
};
</script>
<style lang="scss" scoped>
.pickup {
    font-family: '微軟正黑體', sans-serif;
    background-color: #f1f1f1;
    padding: 0 0 40px 0;
    .auto {
        width: 1440px;
        margin: auto;
        display: flex;
        flex-direction: column;
        @media screen and (max-width: 1440px) {
            width: 1224px;
        }
        @media screen and (max-width: 1224px) {
            width: 96%;
        }
        h2 {
            color: #999;
            font-size: 24px;
            line-height: 40px;
            font-weight: 700;
            padding: 0 0 0 16px;
            margin: 8px;
            position: relative;
            &:before {
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                margin: auto;
                background-color: #999;
                width: 4px;
                height: 24px;
            }
        }
        .section,
        form {
            width: 800px;
            padding: 16px;

            background: #f7f7f7;
            align-self: center;
            font-size: 12px;
            @media screen and (max-width: 1224px) {
                width: 96%;
            }
            .subTitle {
                font-size: 16px;
                line-height: 32px;
                font-weight: 600;
            }
            a.btn {
                color: white;
                margin: 0 0 0 16px;
            }
        }
        form {
            margin: 0 0 40px 0;
        }
    }
}
</style>